@use 'base/variables' as v;
@use 'base/mixins' as m;

.marcas-ganaderia {
    height: 40rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.contenido-marcas{
    display: flex;
    justify-content: center;
    gap: 9rem;
    flex-wrap: wrap;
    @include m.tablet { 
        gap: 3rem;
    }
}
.marcas {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 10rem;
    .marca-producto {
        display: flex;
        align-items: flex-end;
        height: 80%;
        img {
            height: auto;
            width: 20rem;
        }
    }
    .btn-masinfo {
        height: 20%;
        img {
            width: 100%;
            height: 100%;
        }
    }
    
}

.texto-ganaderia {
    margin: -30rem 2.5rem 0rem 2.5rem;
    @include m.telefono { 
        margin: -10rem 3.5rem 0rem 3.5rem;
    }
    @include m.tablet { 
        margin: -15rem 5rem 0rem 5rem;
    }
    @include m.desktop { 
        margin: -25rem 20rem 0rem 20rem;
    }
}

.bg-panal{
    background-image: url('../img/ganaderia/fondo_exagonos.png');
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center top;
}
.logo_posicion_der{
    height: 10rem;
    display: flex;
    justify-content: center;
    .fondo_icon_logo {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 10rem;
        margin: 2.5rem 5rem 0rem 0rem;
        img {
            height: 80%;
        }
    }
}
.bg-ganaderia-black{
    background-image: url('../img/product-ganaderia/bg-ganaderia.jpg');
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center top;
}
.header-detalle-producto-ganaderia{
    padding: 2.5rem 0;
}
.header-contenido-detalle-producto{
    height: 10rem;
    img {
        max-height: 10rem;
        margin: 0 auto;
    }
}
.producto-slice-50{
    display: flex;
    justify-content: center;
    align-items: center;
    img{
        width: 50%;
    }
}

