@use 'base/variables' as v;
@use 'base/mixins' as m;

/**header contacto*/
.header-contenido-contacto{
    height: 10rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@include m.tablet { 
    .header-contenido-contacto{
        height: 10rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items:flex-start;
    }
}

.redes-sociales{
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
    padding: 1.5rem 0rem;
}
@include m.tablet { 
    .redes-sociales{
        display: flex;
        flex-direction: row;
        gap: 0.5rem;
        padding: 2.5rem 0rem 0rem 5rem;
    } 
}
.logo-somos-familia{
    img{
        height: 7.5rem;
    }
}

.fondo_icon_header{
    img{
        height: 4rem;
    }
}

@include m.tablet { 
    .somos-familia{
        padding: 2.5rem 5rem 0rem 0rem;
    }
}
.form-contacto{
    h3 {
        text-align: center;
        font-size: 1.5rem;
        margin-top: 2rem;
        margin-bottom: 3rem;
    }
}
@include m.tablet { 
    .form-contacto{
        
        h3 {
            text-align: left;
            font-size: 2rem;
        }
    }
}
.form-contacto{
    text-align: left;
    padding: 5rem;
}
#map { 
    height: 30rem;
    width: 100%;
    @include m.tablet { 
        width: 70%;    
        margin: 5rem 0;

    } 
}
.contenedor-ubicaciones{
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    @include m.tablet { 
        flex-direction: row;
        gap: 2.5rem;
        justify-content: space-between;
    }
}
.list-sucursales, .sucursal-select{
    margin: 5rem 0;
    span {
        font-size: 1.5Rem;
        text-transform: uppercase;
        color: v.$primario;
    }
    dl {
        padding-left: 2rem;
        margin-top: 0;
        font-size: 1.2rem;
        font-weight:300;
        dd {
            margin: 0;
            font-style: italic;
        }
    }
}
.sucursal-select{
    margin: 0;
}
.formulario {
    // margin: 2rem;
    .campo{
        margin-bottom: 2rem;
        
        &:last-of-type{
            margin-bottom: 0;
        }
    }
    label {
        // text-transform: uppercase;
        display: block;
        font-size: 1.2rem;
        font-weight: 700;
    }
    input:not([type="submit"], [type="radio"], [type="checkbox"]),
        select {
            width: 100%;
            padding: .5rem;
            border: .1rem solid v.$primario;
            border-radius: .5rem;
        }
}
#departamento{
    width: 20rem;
}
.btn-enviar{
    background-color: v.$primario;
    color: v.$blanco;
    border: .1rem solid v.$primario;
    border-radius: .5rem;
    padding: .5rem 1rem;
    cursor: pointer;
    margin-top: 1rem;
    width: 100%;
}
@include m.telefono{ 
    .btn-enviar{
        width: unset;
    }
    // display: inline;
}
.center {
    display: flex;
    justify-content: center;
}
.formulario-select{
    max-width: 25rem;
}

.campo.center{
    margin: 5rem 0;
}
.bg-contacto{
    background-image: url(../img/contacto/fondo-contacto.png);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
    @include m.tablet {
        background-position: bottom; 
    }
}