@use 'base/variables' as v;
@use 'base/mixins' as m;

.bg-nosotros{
    background-image: url('../img/nosotros/bg-nosotros-full.jpg');
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center bottom;
}

.proani-texto{
    text-align: center;
    font-size: 5rem;
    font-weight: 900;
    margin-top: 5rem;
    .pro{
        color: #f19c00;
    }
    .ani{
        color: v.$blanco;
    }
}
@include m.telefono { 
    .proani-texto{
        display: none;
    }
}
.proani-letras{
    img{
        width: 70%;
        margin: 0 auto;
        padding: 5rem 2rem 30rem 2rem;
        display: none;
    }
    @include m.telefono { 
        img{
            padding: 5rem 2rem 45rem 2rem;
            display: block;
        }
    }
    @include m.tablet { 
        img{
            padding: 5rem 2rem 100rem 2rem;
        }
    }
}

.contenedor-nosotoros{
    display: grid;
    grid-template-columns: 100%;
}
@include m.telefono {
    .contenedor-nosotoros{
        grid-template-columns: 30% 70%;
    }  
}
@include m.tablet {
    .contenedor-nosotoros{
        grid-template-columns: 40% 60%;
    }  
}
.historia {
    height: 150vh;
    overflow-y: scroll;
    margin: 2.5rem 0rem 50rem 2.5rem;
    padding-right: 2.5rem;
    @include m.tablet { 
        .historia {
            padding: 5rem;
        }
    }
}

.historia::-webkit-scrollbar{
    width: .7rem;
    height: .7rem;
    background: v.$blanco;
}
.historia::-webkit-scrollbar-thumb{
    background: red;
    border-radius: .7rem;
}

//titulos de las historia

.historia{
    span{
        font-size: 2.5rem;
        display: inline;
        color: v.$blanco;
        margin-right: .5rem;
    }
    h2{
        font-size: 2.5rem;
        color: v.$negro;
    }
}
@include m.tablet { 
    .historia{
        span{
            font-size: 4rem;
            margin-right: .5rem;
        }
        h2{
            font-size: 4rem;
        }
    }
}

.cuadro-historia{
    margin-bottom: 5rem;
    .titulo-historia{
        color: v.$blanco;
        font-size: 5rem;
        font-weight: 900;
        text-align: center;
    }
    .texto-historia{
        border-left: 2px solid v.$negro;
        border-right: 2px solid v.$negro;
        padding: 0 2.5rem;
    }
}
@include m.tablet { 
    .cuadro-historia{
        .titulo-historia{
            font-size: 7rem;
        }
    }
}

.cuadro-padre{
    height: 2px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 -2.5rem;
    .linea-hijo-top{
        width: 10%;
        border-top: 2px solid v.$negro;
    }
    .linea-hijo-bottom{
        width: 10%;
        border-bottom: 2px solid v.$negro;
    }
}

.historia{
    p {
        span {
            font-size: 3rem;
            color: v.$negro;
            line-height: 1;
        }
    }
}