@use 'base/variables' as v;
@use 'base/mixins' as m;

.guia-gato{
    padding-top: 0;
}
@include m.tablet { 
    .guia-gato{
        padding-top: 5rem;
    }
}
.contenedor-gato{
    display: grid;
    grid-template-columns: 100%;
    .content-gato{
        margin-top: 5rem;
    }
    div:nth-of-type(1)
    {
        order: 2;
    }
}
@include m.tablet { 
    .contenedor-gato{
        grid-template-columns: 40% 60%;        
        div:nth-of-type(2)
        {
           order: 2;
        }
    }
    
}
@include m.desktop { 
    .contenedor-gato{
        grid-template-columns: 30% 70%;        
    }
    
}
.content-gato{
    padding: 0 5rem;
}
.content-formulario{
    padding:0 2rem;
}
@include m.tablet { 
    .content-formulario{
        padding-left: 3rem;
    }
}
.content-gato{
    .logo{
        img{
            margin: 0 auto;
            height: 10rem;
        }
    }
    .gato{
        img{
            width: 70%;
            margin: 0 auto;
        }
    }
}

/** tabla guia de alimentacion*/
.contenedor-tabla-guia{
    margin: 2rem 2rem 0 2rem;
    label{
        padding-bottom: 2.5rem;
    }
    table{
        border: 1px solid;
        width: 100%;
        margin: 2.5rem auto 0;
    }
}
@include m.tablet { 
    .contenedor-tabla-guia{
        margin: 5rem 5rem 0 5rem;
        table{
            width: 70%;
        }
    }
}
.tabla-guia{
    // border-collapse: collapse;
    border-radius: 1em;
    overflow: hidden;
    td {
        padding: 1em;
        border-bottom: 1px solid v.$negro; 
    }
    tr:last-child {
        td:last-child {
            border-bottom: v.$blanco;
        }
    }
      
}
.registro-mascota{
    padding: 2rem;
}
@include m.tablet { 
   .registro-mascota{
        padding: 5rem;
    } 
}

.d-bloque{
    display: block;
    width: 100%;
}
.campo-fila{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1rem;
}
@include m.telefono { 
    .campo-fila{
        grid-template-columns: repeat(2, 1fr);
    }
}
@include m.tablet { 
    .campo-fila{
        grid-template-columns: repeat(4, 1fr);
    }
}

.formulario-registro {
    label{
        margin-bottom: 0.5rem;
    }
    .campo{
        margin-bottom: 2rem;
        
        &:last-of-type{
            margin-bottom: 0;
        }
    }
    label {
        // text-transform: uppercase;
        display: block;
        font-size: 1.2rem;
        font-weight: 700;
        margin-top: 0.5rem;
    }
    input:not([type="submit"], [type="radio"], [type="checkbox"]),
        select {
            width: 100%;
            padding: .5rem;
            border: .1rem solid v.$primario;
            border-radius: .5rem;
        }
}

.titulo-guia{
    color: v.$primario;
    font-weight: bold;
}